// color scheme
$background: #1A1918;
$primary: #FF752B;
$secondary: #00B3AC;

.main-view {
  background-color: $background;
  color: #fff;
}

.navbar {
  background-color: $secondary;
}

.movie-card-container {
  padding: 0;
  .movie-card {
    border: none;
    box-shadow: 0 5px 10px darken($background, 10%);
    .card-body {
      background-image: linear-gradient(160deg, darken($primary, 25%), darken($primary, 15%));
      
    }
    button {
      background-color: darken($primary, 7%);
      border: none;
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);

      &:hover,
      &:focus,
      &:active {
        background-color: $primary;
      }
    }
  }
}
